*{
  font-family: Helvetica, Arial, sans-serif;
}

body{
  padding: 60px;
  background-color: rgb(87, 87, 87);
}

input{
  color: #212121;
  font-size: 1.2rem;
  width: 100%;
}
.title{
  font-size: 150px;
}

.card-grid{
  display: flex;
  flex-direction: row ;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p{
  padding: 5px;
  text-align: center;
  color: white;
}

.card img{
  max-height: 170px;
}
